import React from 'react'
import {Modal,Form,Input,Select,Button,InputNumber} from 'antd';
import {createBuyer} from '../../../api/buyer';
import {notifyScreen} from '../../../utils/notify'

const ModalAddBuyer = ({hideModal,handleHideModal}) => {
    const handlerFormAdd=async (values)=>{
        try {
            let code = values.code || "";
            let name = values.name || "";
            let age = values.age || "";
            let sex = values.sex || "";
            let phone = values.phone || "";
            let address = values.address || "";
            let note = values.note || "";
            let buyer = {code,name,phone,address,note,sex,age}
            let res = await createBuyer(buyer);
            if (res.status === 200) {
              notifyScreen("success", res.data.statusCode, res.data.message);
              return handleHideModal();
            }
          } catch (error) {
            if (error.response) {
              if (error.response.data) {
                notifyScreen("error",error.response.data.statusCode,error.response.data.message)
              }
            }else{
              notifyScreen("error","500","Li khng xc nh")
            }
        }
      }
    return (
        <Modal visible={hideModal} onCancel={handleHideModal} title="Thm khch hng" footer={null}>
            <Form onFinish={handlerFormAdd}>
                <span>M KH</span>
                <Form.Item 
                name="code"
                rules={[{ required: true, message: "Vui lng nhp m KH!" }]}
                >
                    <Input placeholder="Nhp m KH - VD : 0001"/>
                </Form.Item>
                <span>Tn</span>
                <Form.Item 
                name="name"
                rules={[{ required: true, message: "Vui lng nhp tn!" }]}
                >
                    <Input placeholder="Nhp tn"/>
                </Form.Item>
                <span>Tui</span>
                <Form.Item 
                name="age"
                >
                    <InputNumber placeholder="Nhp tui"/>
                </Form.Item>
                <span>Gii tnh</span>
                <Form.Item name="sex" rules={[{ required: true, message: "Vui lng chn gii tnh!" }]}>
                    <Select placeholder="--La chn--">
                        <Select.Option key="1" value="male">
                            Nam 
                        </Select.Option>
                        <Select.Option key="2" value="female">
                            N
                        </Select.Option>
                    </Select>
                </Form.Item>
                <span>S in thoi</span>
                <Form.Item name="phone">
                    <Input placeholder="Nhp s in thoi"/>
                </Form.Item>
                <span>a ch</span>
                <Form.Item name="address">
                    <Input placeholder="Nhp a ch"/>
                </Form.Item>
                <span>Ghi ch</span>
                <Form.Item name="note">
                    <Input.TextArea placeholder="Nhp ghi ch"/>
                </Form.Item>
                <div style={{display:"flex",justifyContent:"flex-end"}}>
                    <Form.Item style={{marginRight:"10px"}}>
                        <Button type="primary" danger onClick={handleHideModal}>Thot</Button>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">Thm</Button>
                    </Form.Item>
                </div>
            </Form>
        </Modal>
    )
}

export default ModalAddBuyer
