import React from "react";
import { Modal, Form, Input, Button } from "antd";
import {createPartner} from '../../../api/partner';
import {notifyScreen} from '../../../utils/notify'

const ModalAddPartner = ({ hideModal, handleHideModal }) => {
  const handlerFormAdd=async (values)=>{
    try {
        let code = values.code || "";
        let name = values.name || "";
        let nameCompany = values.nameCompany || "";
        let phone = values.phone || "";
        let email = values.email || "";
        let address = values.address || "";
        let note = values.note || "";
        let partner = {code,name,nameCompany,phone,email,address,note}
        let res = await createPartner(partner);
        if (res.status === 200) {
          notifyScreen("success", res.data.statusCode, res.data.message);
          return handleHideModal();
        }
      } catch (error) {
        if (error.response) {
          if (error.response.data) {
            notifyScreen("error",error.response.data.statusCode,error.response.data.message)
          }
        }else{
          notifyScreen("error","500","Li khng xc nh")
        }
    }
  }

  return (
    <Modal
      visible={hideModal}
      onCancel={handleHideModal}
      title="Thm nh cung cp"
      footer={null}
    >
      <Form onFinish={handlerFormAdd}>
        <span>M NCC</span>
        <Form.Item
          name="code"
          rules={[{ required: true, message: "Vui lng nhp m nh cung cp!" }]}
        >
          <Input placeholder="Nhp m - VD : 0001" required />
        </Form.Item>
        <span>Tn</span>
        <Form.Item
          name="name"
          rules={[{ required: true, message: "Vui lng nhp tn" }]}
        >
          <Input placeholder="Nhp tn" />
        </Form.Item>
        <span>Cng ty</span>
        <Form.Item
          name="nameCompany"
          rules={[{ required: true, message: "Vui lng nhp tn cng ty!" }]}
        >
          <Input placeholder="Nhp cng ty" />
        </Form.Item>
        <span>S in thoi</span>
        <Form.Item 
           name="phone"
        >
          <Input placeholder="Nhp s in thoi" />
        </Form.Item>
        <span>Email</span>
        <Form.Item
           name="email"
        >
          <Input placeholder="Nhp email" />
        </Form.Item>
        <span>a ch</span>
        <Form.Item
           name="address"
        >
          <Input placeholder="Nhp a ch" />
        </Form.Item>
        <span>Ghi ch</span>
        <Form.Item
           name="note"
        >
          <Input.TextArea placeholder="Nhp ghi ch" />
        </Form.Item>
        <div style={{display:"flex",justifyContent:"flex-end"}}>
          <Form.Item style={{marginRight:"10px"}}>
            <Button type="primary" danger onClick={handleHideModal}>
              Thot
            </Button>
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">Thm</Button>
          </Form.Item>
        </div>
      </Form>
    </Modal>
  );
};

export default ModalAddPartner;
